<template>
  <example-standing-h :my-list="list">

  </example-standing-h>
</template>

<script>
import{reactive,onMounted} from 'vue';
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
const ExampleStandingH=defineAsyncComponent(()=>import('@/components/main/ExampleStandingH'))
import {handleError} from "@/components/composable/handleError";

export default {
  name: "StandingHome",
  components: {ExampleStandingH},
  setup(){
    const list=reactive([]);
    const sum=onMounted(()=>{
      EndPoints.getGoalsWorldcup()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data.standing.standings_home)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      list,
      sum,
    }
  }
}
</script>

