<template>
<section id="scroll" class="scroll center-table bg-white border border-slate-300 rounded-sm p-1" :style="{height:heightText+'px'}">
  <div class="container mx-auto p-1">
    <div  class="w-full h-auto ">
      <p class="text-start text-slate-900 text-semibold">جدول رده بندی</p>
      <div class="w-full h-0.5 bg-gray-800 my-2"></div>
      <div class="flex flex-row justify-start">
        <div @click="getKey(1)"><button type="button" :class="{bgGreen : count===1 , bgWhite: count===2}"  class="btn text-sm text-slate-600 text-center p-1 rounded-sm outline-none mx-3" @click="tabs='AllStandings'">همه</button></div>
        <div @click="getKey(2)"><button type="button" :class="{bgGreen : count===2 , bgWhite: count===1}"  class="btn text-sm text-slate-600 text-center p-1 rounded-sm outline-none mx-3" @click="tabs='StandingHome'">میزبان</button></div>
        <div @click="getKey(3)"><button type="button" :class="{bgGreen : count===3 , bgWhite: count===2}"  class="btn text-sm text-slate-600 text-center p-1 rounded-sm outline-none mx-3" @click="tabs='StandingsAway'">مهمان</button></div>
      </div>
      <div class="flex flex-row align-center justify-end">
        <span class="text-sm text-slate-500 ml-3" :style="{size:sizeText}">بازی</span>
        <span class="text-sm text-slate-500 ml-3" :style="{size:sizeText}">برد</span>
        <span class="text-sm text-slate-500 ml-3" :style="{size:sizeText}">مساوی</span>
        <span class="text-sm text-slate-500 ml-3" :style="{size:sizeText}">باخت</span>
        <span class="text-sm text-slate-500 ml-3" :style="{size:sizeText}">تفاضل</span>
        <span class="text-sm text-slate-500 ml-3" :style="{size:sizeText}">گل ها/+</span>
        <span class="text-sm text-slate-500 ml-3" :style="{size:sizeText}">امتیاز</span>
      </div>
      <div class="bg-slate-400 w-full h-0.5 my-1"></div>
    </div>
    <keep-alive>
      <component :is="tabs"></component>
    </keep-alive>
    <div class="flex flex-row w-full justify-start">
      <input type="checkbox" class="bg-blue-600 mx-1">
      <p class="text-right font-semibold text-start mx-1">یک هشتم نهایی</p>
    </div>
  </div>
</section>
</template>

<script>
import{ref} from 'vue'
import AllStandings from "@/components/main/TableTeams/AllStandings";
import StandingHome from "@/components/main/TableTeams/StandingHome";
import StandingsAway from "@/components/main/TableTeams/StandingsAway";
export default {
  name: "TableWorldcup",
  components: {StandingsAway, StandingHome, AllStandings},
  setup(){
    const heightText=ref(600)
    const count=ref(1)
    const sizeText=ref(10)
    const tabs=ref('AllStandings')
    function getKey(items){
      count.value=items
    }
    return{
      count,
      heightText,
      sizeText,
      tabs,
      getKey,

    }
  }
}
</script>

<style scoped>

.bgGreen {
  background-color: green;
  color: #fff;
}
.bgWhite{
  background-color:#fff
}
.scroll{
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color: gray;
}
#scroll::-webkit-scrollbar-track{
  background-color: #fff;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
</style>