<template>
<example-standing-h :my-list="menu">

</example-standing-h>
</template>

<script>
import{reactive,onMounted} from 'vue';
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
const ExampleStandingH=defineAsyncComponent(()=>import('@/components/main/ExampleStandingH'))
import {handleError} from "@/components/composable/handleError";

export default {
  name: "AllStandings",
  components: {ExampleStandingH},
  setup(){
    const menu=reactive([])
    const showInfo=onMounted(()=>{
      EndPoints.getGoalsWorldcup()
      .then((response)=>{
        if(response.data.success){
          menu.push(response.data.data.standing.standings)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      menu,
      showInfo,

    }
  }
}
</script>

