<template>
<example-all-news :my-list="list">

</example-all-news>
</template>

<script>
import{reactive,onMounted} from 'vue'
import EndPoints from "@/service/EndPoints";
import ExampleAllNews from "@/components/main/ExampleAllNews";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "NewsWorldcup",
  components: {ExampleAllNews},
  setup(){
    const list=reactive([])
    const total=onMounted(()=>{
      EndPoints.getNewsWorldcup()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      list,
      total,

    }
  }
}
</script>

<style scoped>
.scroll{
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color: gray;
}
#scroll::-webkit-scrollbar-track{
  background-color: #fff;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
</style>