<script>
import{ref} from 'vue'
import TotalWorldcup from "@/components/main/worldCup/TotalWorldcup";
import BestGoalsWorldcup from "@/view/BestGoalsWorldcup";
import AssistGoalsWorldcup from "@/components/main/worldCup/assistGoalsWorldcup";


