  name: "BestWorldcupt",
  components: {AssistGoalsWorldcup, BestGoalsWorldcup, TotalWorldcup},
  setup(){
    const heightBox=ref(600)
    const count=ref(1)
    const tabs=ref('BestGoalsWorldcup')
    function bestGoals(items){
      count.value=items;
    }
    return{
      count,
      tabs,
      heightBox,
      bestGoals,
    }
  }
}
</script>

