<template>
  <example-final-leage :my-list="menu">

  </example-final-leage>
</template>

<script>
import{reactive,onMounted,ref} from 'vue';
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
const ExampleFinalLeage=defineAsyncComponent(()=>import('@/components/main/ExampleFinalLeage'))
import {handleError} from "@/components/composable/handleError";

export default {
  name: "FinalStandings",
  components: {ExampleFinalLeage},
  setup(){
    const heightText=ref(600)
    const sizeText=ref(10)
    const menu=reactive([])
    const result=onMounted(()=>{
      EndPoints.getGoalsWorldcup()
      .then((response)=>{
        if(response.data.success){
          menu.push(response.data.data.week)
          console.log(menu)
        }
      })
      .catch((error)=>{
       handleError(error)
      })

    })
    return{
      heightText,
      menu,
      sizeText,
      result,

    }
  }
}
</script>

<style scoped>
.scroll{
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
#scroll::-webkit-scrollbar{
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color: gray;
}
#scroll::-webkit-scrollbar-track{
  background-color: #fff;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
</style>